export default {
    data() {
        return {
            Answered: false,
            index: this.$route.params.index,
            fomoReceiver: this.$route.params.fomoLv,
            selectedAns: ' ',

            questions: [
                {
                    question: "Què signifiquen les sigles de FoMo?",
                    answer: { a: 'Friends of Marco organization', b: 'Fear of Missing out', c: 'Fathers out, Mothers out', d: 'Financial oportunity Missed out' },
                    correct: 'b',
                },
                {
                    question: "El FoMo és:",
                    answer: { a: "La sensació d'ansietat que es produeix al pensar que pots perdret un esdeveniment.", b: "La por a estar massa estona connectat.", c: "La ansietat que es genera al pensar que et pots perdre en un bosc.", d: "Cap de les anteriors." },
                    correct: 'a',
                },
                {
                    question: "A quin grup de edat pot afectar més el FoMo?",
                    answer: { a: 'Gent gran', b: 'Adults', c: 'Adolescents', d: 'Cap dels anterios' },
                    correct: 'c',
                }
            ],

            sabies: [
                "Gastem més de 6:30 hores diarament a internet a arreu el món. A Espanya, en particular, la mitja és una mica més baixa però supera les 5:30 hores.",
                "Els sintomes del FoMo poden arribar a ser: ansietat, inquietud, constant comparació, poca energia i incapacitat per concentrar-se entre altres.",
                "El JoMo (Joy of Missing out) es la satisfacció de perdret coses. Alguns habits com deixar de fer moltes tasques a la vegada, fer respiracions profundes poden ajudar a aumentar-lo"
            ],

            feel: [
                "Et sents ple de energia, feliç i amb moltes ganes de fer coses.",
                "Et sents bastant bé i feliç.",
                "Estas content al final del dia.",
                "Estas una mica trist.",
                "Estas bastant trist, notes que necessites mirar el mobil avans d'anar a dormir.",
                "Estas molt trist. L'unica cosa que et ve de gust per calmar l'ansietat es estar més estona a les xarxes socials, per no perdre't cap notificació."
            ],

        }

    },
    methods: {
        selected(c) {
            this.selectedAns = c.target.value;
            this.Answered = true;
        },
        incrementIndex() {
            this.index++;

            this.$router.push({
                name: 'intro', params: {
                    index: this.index,
                    fomo: this.fomoReceiver,
                }
            })
        },

    }
}